 /*!
  * README
  * Must give vimeo iframe's class="vimeo-player" AND Unique id e.g. id="vim1"
  * ALSO requires e.g. player_id=vim1 in addition to the standard api=1 in the Vimeo Embed URL
  */

(function($) {
  "use strict";

  var all = {},
      all = $('.vimeo-player');
  var videos = {};
  var videoInstance = [];
  var domain = 'leankit.com';

  $.each(all, function( index, value ) {
    var x = $(this),
    id = x.attr('id'),
    url = x.attr('src').split('?')[0],      // Source URL
    vimeoID = url.split('player.vimeo.com/video/')[1],
    protocol = document.URL.split(':')[0],  // Domain protocol (http or https)
    trackProgress = x.data('progress'),     // Data attribute to enable progress tracking
    trackSeeking = x.data('seek'),          // Data attribute to enable seek tracking
    gaTracker,
    progress25 = false,
    progress50 = false,
    progress75 = false,
    videoPlayed = false,
    videoPaused = false,
    videoResumed = false,
    videoSeeking = false,
    videoCompleted = false,
    timePercentComplete = 0;

    // Match protocol with what is in document.URL
    if (url.match(/^http/) === null) {
      url = protocol + ':' + url;
    }

    // Check which version of Google Analytics is used

    // Universal Analytics (universal.js)
    if (typeof ga === "function") {
      gaTracker = 'ua';
      // console.info('Universal Analytics');
    }

    // Classic Analytics (ga.js)
    if (typeof _gaq !== "undefined" && typeof _gaq.push === "function") {
      gaTracker = 'ga';
      //console.info('Classic Analytics');
    }

    // Google Tag Manager (dataLayer)
    if (typeof dataLayer !== "undefined" && typeof dataLayer.push === "function") {
      gaTracker = 'gtm';
      // console.info('Google Tag Manager');
    }
    // Listen for messages from the player
    if (window.addEventListener) {
      window.addEventListener('message', onMessageReceived, false);
    } else {
      window.attachEvent('onmessage', onMessageReceived, false);
    }

    // Push the object to videos array, so we can watch multiple videos on the same page
    videos[id] = [];
    videos[id].push({
      url: url,
      id: id,
      vimeoID: vimeoID,
      protocol: protocol,
      trackProgress : trackProgress,     // Data attribute to enable progress tracking
      trackSeeking : trackSeeking,          // Data attribute to enable seek tracking
      gaTracker : gaTracker,
      progress25 : progress25,
      progress50 : progress50,
      progress75 : progress75,
      videoPlayed : videoPlayed,
      videoPaused : videoPaused,
      videoResumed : videoResumed,
      videoSeeking : videoSeeking,
      videoCompleted : videoCompleted,
      timePercentComplete : timePercentComplete
    });
  });

  // Handle messages received from the player
  function onMessageReceived(e) {

    var data = JSON.parse(e.data);

    // Ignore any messages not worth receiving
    if (! data.player_id) {
      return;
    }

    var videoInstance = videos[data.player_id][0];

    switch (data.event) {
    case 'ready':
      onReady(videoInstance);
      break;

    case 'playProgress':
      onPlayProgress(data.data, videoInstance);
      break;

    case 'seek':
      if (videoInstance.trackSeeking && !videoInstance.videoSeeking) {
        sendEvent('Skipped video forward or backward', videoInstance);
        videoInstance.videoSeeking = true; // Avoid subsequent seek trackings
      }
      break;

    case 'play':
      if (!videoInstance.videoPlayed) {
        sendEvent('Started video', videoInstance);
        videoInstance.videoPlayed = true; // Avoid subsequent play trackings
      } else if (!videoInstance.videoResumed && videoInstance.videoPaused) {
        sendEvent('Resumed video', videoInstance);
        videoInstance.videoResumed = true; // Avoid subsequent resume trackings
      }
      break;

    case 'pause':
      onPause(videoInstance);
      break;

    case 'finish':
      if (!videoInstance.videoCompleted) {
        sendEvent('Completed video', videoInstance);
        videoInstance.videoCompleted = true; // Avoid subsequent finish trackings
      }
      break;
    }
  }


  // Helper function for sending a message to the player
  function post(action, value, videoInstance) {
    var vid = $("#" + videoInstance.id);
    var data = {
      method: action
    };

    if (value) {
      data.value = value;
    }

    vid[0].contentWindow.postMessage(JSON.stringify(data), videoInstance.url);
  }

  // Send event to Classic Analytics, Universal Analytics or Google Tag Manager
  function sendEvent(action, videoInstance) {

    var url = videoInstance.url;
    var gaTracker = videoInstance.gaTracker;

    switch (gaTracker) {
    case 'gtm':
      dataLayer.push({
        'event' : 'Vimeo',
        'eventCategory' : 'Vimeo',
        'eventAction' : action,
        'eventLabel' : url,
        'eventValue' : undefined,
        'eventNonInteraction' : true
      });
      break;
    case 'ua':
      ga('send', 'event', 'Vimeo', action, url, undefined, {
        "nonInteraction" : 1
      });
      break;
    case 'ga':
      _gaq.push(['_trackEvent', 'Vimeo', action, url, undefined, true]);
      break;
    }
  }

  // Add additional EventListeners once the videos are ready
  function onReady(videoInstance) {
    post('addEventListener', 'play', videoInstance);
    post('addEventListener', 'seek', videoInstance);
    post('addEventListener', 'pause', videoInstance);
    post('addEventListener', 'finish', videoInstance);
    post('addEventListener', 'playProgress', videoInstance);
  }

  // Tracking video progress
  function onPlayProgress(data, videoInstance) {
    videoInstance.timePercentComplete = Math.round((data.percent) * 100); // Round to a whole number

    if (!videoInstance.trackProgress) {
      return;
    }

    videoInstance.progress = false;

    if (videoInstance.timePercentComplete > 24 && !videoInstance.progress25) {
      videoInstance.progress = 'Played video: 25%';
      videoInstance.progress25 = true;
    }

    if (videoInstance.timePercentComplete > 49 && !videoInstance.progress50) {
      videoInstance.progress = 'Played video: 50%';
      videoInstance.progress50 = true;
    }

    if (videoInstance.timePercentComplete > 74 && !videoInstance.progress75) {
      videoInstance.progress = 'Played video: 75%';
      videoInstance.progress75 = true;
    }

    if (videoInstance.progress) {
      sendEvent(videoInstance.progress, videoInstance);
    }
  }

  function onPause(videoInstance) {
    if (videoInstance.timePercentComplete < 99 && !videoInstance.videoPaused) {
      sendEvent('Paused video', videoInstance);
      videoInstance.videoPaused = true; // Avoid subsequent pause trackings
    }
  }

  $('.infodot_getting-started .modal').on('shown.bs.modal', function (e) {
    var targetIframe = $(this).find('.vimeo-player').attr('id');
    if (targetIframe) post('play', null, videos[targetIframe][0]);
  })

  $('.infodot_getting-started .modal').on('hide.bs.modal', function (e) {
    var targetIframe = $(this).find('.vimeo-player').attr('id');
    if (targetIframe) post('pause', null, videos[targetIframe][0]);
  })
})(jQuery);



